.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-align: center;

  min-height: 100vh;
}
.app.spine_aligned {
  text-align: unset;
  align-items: flex-start;
}
.app.spine_aligned:dir(rtl) {
  align-items: flex-start;
}

.app .contentWrapper{
  width: 1000px;
  max-width: 100%;
  margin: 0 auto;
  padding-bottom: var(--basis_x16);
}
@media (max-width: 800px) {
  .app .contentWrapper{
    width: 100%;
  }
}

.editor {
  min-height: 100vh;
  padding: var(--basis_x4);
  transition: opacity var(--timing);
}
.editor.loadingContent {
  opacity: 0.5;
  pointer-events: none;
}

@media (min-width: 800px) {
  .editor_form_row {
    display: flex;
    flex-direction: row;
    margin-bottom: var(--basis_x8);
  }
  .editor_form_row > * {
    flex: 1;
  }
  .editor_form_row > h3 {
    max-width: 200px;
    margin: 0;
    margin-right: var(--basis_x2);
  }
}
